@font-face {
  font-family: 'IBMPlexSansKR-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-07@1.0/IBMPlexSansKR-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: #F0F0F0;
  font-family: 'IBMPlexSansKR-Regular';
}

body::-webkit-scrollbar { width: 5px; }
body::-webkit-scrollbar-thumb { background-color: #bbb; border-radius: 10px; }
body::-webkit-scrollbar-track { background-color: #ddd; border-radius: 10px; }
body::-webkit-scrollbar-button:start,
body::-webkit-scrollbar-button:end { opacity: 0; height: 5px; }

.container {
  width: 100%;
  padding: 80px 0;
  margin: 0 auto;
}

@media (max-width: 576px) {
  .menu-list {
    display: block !important;
  }

  .menu-list > .menu-btn:first-child {
    margin-bottom: 20px;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 1400px) {
  .container {
    max-width: 1280px;
  }
}

input[type=number] {
  width: calc(100% - 10vw);
  margin: 0 auto;
  background-color: #ffffff70;
  border: 1px solid #ddd;
  border-left: 0;
  border-right: 0;
  border-top: 0;
  padding: 15px;
  display: block;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[class^=etc-text] {
  width: max-content;
  padding: 0;
  margin: 0 10px;
}

.d-flex {
  display: flex;
}

.radioGroup {
  width: calc(100% - 10vw);
  margin: 0 auto;
  display: flex;
}

.radioGroup > label {
  margin-right: 5px;
}

.title {
  font-size: 36px;
  text-align: center;
  margin: 40px 0;
}

.menu-list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 40px auto;
}

.menu-list .menu-btn {
  flex: 1;
  text-align: center;
  margin: 0 10px;
  padding: 8px 16px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #b8c9db;
  background-color: #dfe8ef;
  color: #111;
  cursor: pointer;
  font-size: 16px;
}

.menu-list .menu-btn.selected {
  border: 1px solid #819fc0;
  background-color: #b8c9db;
}

.content {
  padding: 40px 20px;
}

.result-btn {
  width: 80%;
  padding: 10px 20px;
  display: block;
  margin: 60px auto;
}

.result {
  position: relative;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  margin: 80px 0;
}

.result > p {
  position: absolute;
  width: 100%;
  height: 1px;
  top: -5px;
  background-color: #e5e5e5;
}

.result > span {
  position: absolute;
  background-color: #f0f0f0;
  padding: 0 10px;
  left: calc(50% - 26px);
}

.result-content {
  font-weight: 500;
  padding: 60px 0;
  font-size: 16px;
  text-align: center;
}

.c-table {
  width: 100%;
}

.c-table th {
  text-align: right;
  padding: 20px 0;
}

.d-none {
  display: none;
}